.ContactForm{
    margin:4rem 6rem;
    color:#2a2a2a;
}

.ContactForm form{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width:50%;
    margin:auto;
}

.ContactForm input{
    height: 3rem;
    padding:0 1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border:1px solid #2a2a2a;
}

.ContactForm textarea{
    
    padding: 1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border:1px solid #2a2a2a;
}
.ContactForm button{
    display: inline;
}

@media screen and (max-width:850px) {

    .ContactForm{
        margin:4rem 2rem;
        color:#2a2a2a;
    }
    
    .ContactForm form{
        padding-top: 2rem;  
        width:90%;
    }
}