.NavbarItems{
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 30px;
    box-shadow: 0 15px 5px 0 rgba(0,0,0,0.25);
    width:95%;
    height: 80px;
    border-radius: 13px;
    position:fixed;
    top:10px;
    left:50%;
    transform: translate(-50%);
    z-index: 99999;
 
}
.Navbar-logo{
color:#222;
font-size: 2rem;
cursor: pointer;
white-space: nowrap;
}

.Navbar-menu{
    display:grid;
    grid-template-columns: repeat(6,auto);
    grid-gap:1.2rem;
    align-items:center;
    list-style: none;
}
.nav-links{
   text-decoration: none;
   color:#222;
   font-size: 1.2rem;
   font-weight:600;
   padding:0.6rem 0.5rem;
   white-space: nowrap;
}
.nav-links i{
    padding-right:0.3rem;
}
.nav-links:hover{
background-color: #01959a;
color:#fff;
border-radius: 4px;
transition: all 0.2s ease-in-out;
}

.nav-links-mobile{
    display: none;
}

button{
    padding:0.5rem 0.7rem;
    border-radius:0.3rem ;
    white-space:nowrap;
    font-size:1rem;
    font-weight: 600;
    border: none;
    text-decoration: none;;

}
button:hover{
cursor: pointer;
background-color: #151516;
color:#fff;
transition: 0.2s ease-in-out; 

}

.menu-icons{
    display:none;
}



@media screen and (max-width:914px){

    .NavbarItems{
        z-index: 99;
    }
    .Navbar-menu{
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
        list-style: none;
        background: #fff;;
        width: 100%;
        height: auto;
        position:absolute;
        top:0;
        left:-100%;
        opacity: 0;
        align-items:stretch ;
        padding:80px 0px 30px 0px;
        margin:0px; 
        transition: 0.3s ease-in-out;
        border-radius: 13px;
        
    }

    .Navbar-menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out
    }

    .nav-links{
        display:block;
        width:100%;
        font-size:1.2rem;
        padding:2rem 0;
    }

    .nav-links:hover{
        background:#01959a;
        transition:none;
    }

    button{
        display: none;
    }

    .nav-links-mobile{
        display: block;
        text-align: center;
        padding:1.5rem;
        width: 80%;
      margin:auto;
        border-radius: 4px;
        background: #01959a;
        text-decoration: none;
        color:#fff;
        font-size:1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
    }
    
    .menu-icons{
        display: block;
    }

    .menu-icons i{
        font-size: 1.2rem;
        color:#222;
        cursor:pointer;
    }
}