.trip{
    margin:4rem 6rem;
}
.trip h1{
    font-size: 3rem;
   
}
 .tripcard{
margin-top:3rem;
display:flex;
justify-content: space-between;
    }

.t-card{
    width:32%;
    text-align: start;
    box-shadow: 0px 5px 25px 2px rgba(0,0,0,0.11);
   border-radius: 7px;
   cursor: pointer;
   padding:1rem 0.8rem;

}
.t-card h1{
    font-size: 1.3rem;
    font-weight: 800;
    padding:0.9rem 0 0.2rem 0;
}

.t-image{
height: 200px;
overflow: hidden;
border-radius: 7px;
}

.t-image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius:7px;

}

.t-image img:hover{
    transform: scale(1.2);
}

@media screen and (max-width:850px) {


    .trip{
        margin:4rem 2rem;;
    }
    .tripcard{
        margin-top:3rem;
        display:flex;
        flex-direction: column;
        justify-content: space-between;
     }   

     .t-card{
        width:100%;
        margin-bottom: 1.5rem;
    }
    
}